import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import { ContactsForm, Name } from "./Form.styled";
import { addContact } from "redux/operations";

export const Form = () => { 
    const dispatch = useDispatch();
    const contacts = useSelector(state => state.contacts.contacts.items)
    
    const [name, setName] = useState('');
    const [number, setNumber] = useState('');

    const handleInputChange = (e) => { 
        if (e.currentTarget.name === 'name') { 
            setName(e.currentTarget.value)
        }
        if (e.currentTarget.name === 'number') { 
            setNumber(e.currentTarget.value)
        }
    }

    const onSubmit = (e) => {
        e.preventDefault();

        const newContact = {
            name: e.currentTarget.elements.name.value,
            number: e.currentTarget.elements.number.value
        }

        const isNameInContacts = contacts.filter(item => item.name === e.currentTarget.elements.name.value).length;

        (isNameInContacts) ?
        alert("This name is already in contacts"):
        dispatch(addContact(newContact));
        
        resetForm();
    }

    const resetForm = () => {
        setName('');
        setNumber('');
    }

    return (
            <ContactsForm onSubmit={onSubmit}>
                <label>
                    <Name>Name</Name>
                    <TextField sx={{marginBottom: '10px'}}
                        type="text"
                        name="name"
                        pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                        title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
                        value={name}
                        onChange={handleInputChange}
                        required
                    />
                </label>
                <label>
                    <Name>Number</Name>
                    <TextField sx={{marginBottom: '10px'}}
                        type="tel"
                        name="number"
                        pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                        title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
                        value={number}
                        onChange={handleInputChange}
                        required
                    />
                </label>
                <Button sx={{marginLeft: '20px'}} variant="outlined" color="success" type="submit">Add contact</Button>
            </ContactsForm>)
}